export default {
	data() {
		return {
			HEIGHT: 0, //屏幕高度
			result: [], //监听的结果
			current: [], //当前在屏幕内的
			sum: 0, //加载完成个数
			comeani: 'come-ani', //入场动画
			isScroll: true, //是否加载动画
		}
	},
	props: {
		location: {
			type: String,
			default: 'center',
		},
		title: {
			type: String,
			default: ''
		},
		list: { 
			type: Array
		}
	},
	computed: {
		addTypeClass() {
			let _class = "";
			let arrType = ["left", "right", "center"];
			if (arrType.indexOf(this.location) !== -1 && this.location !== 'center') {
				_class += this.location + "-time-line";
			}
			return _class;
		}
	},
	onPageScroll() {
		this.$refs.timeline.getScroll();
	},
	mounted() {
		this.init();
	},
	methods: {
		init() {
			try {
				// 获取屏幕高度
				const res = uni.getSystemInfoSync();
				this.HEIGHT = res.screenHeight;
				// 添加标志位
				for (let i = 0; i < this.list.length; i++) {
					this.current.push({
						tag: 'timeline' + i,
						is: 'no'
					});
				}
				// 开始获取位置信息
				this.getScroll();
			} catch (e) {
				// error
			}

		},
		loadani() {
			for (let i = 0; i < this.result.length; i++) {
				for (let j = 0; j < this.result[i].info.length; j++) {
					// 是否没加载动画
					if (this.current[j].is != 'ok') {
						// 是否进入视野
						if (this.current[j].tag == this.result[i].info[j].tag &&
							this.result[i].info[j].domTop + 90 < this.HEIGHT) {
							// 加载动画
							this.current[j].is = 'ok';
							this.sum = j + 1;
						}
					}
				}
				// 移除当前
				this.result.splice(i, 1);
			}
			// 是否全部加载完成
			if (this.sum == this.list.length) {
				this.isScroll = false;
			}
		},
		async getScroll() {
			if (!this.isScroll) {
				return;
			}
			let info = [];
			// 返回位置信息加入数组
			for (let i = 0; i < this.list.length; i++) {
				await this.getNodeList('timeline' + i, i).then(res => {
					info.push(res);
				});
			}
			this.result.push({
				info: info
			});
			// 加载动画
			this.loadani();
		},
		getNodeList(id, i) {
			// 获取位置信息并返回
			return new Promise(resolve => {
				const query = uni.createSelectorQuery().in(this);
				query.select('#' + id).boundingClientRect(data => {
					// console.log("得到布局位置信息" + JSON.stringify(data));
					// console.log("节点离页面顶部的距离为" + data.top);
					resolve({
						domInfo: data.height,
						domTop: data.top,
						tag: id
					})
				}).exec();
			});
		},
		// 添加动画
		layoutClass(index) {
			let _class = "";
			if (this.location == 'center' && index % 2 != 0) {
				_class = 'right-info';
			}
			return _class;
		}
	},
}
